import React from "react";
import { Link } from "react-router-dom";
import HtmlRender from "./HtmlRender";

function Thumbnail({ post }) {
  const EXCERPT_LENGTH = 200;
  if (post) {
    const date = new Date(post.date);
    post.dateString =
      date.getDate() + "-" + (date.getMonth() + 1) + "-" + date.getFullYear();
  }
  return (
    <div className="container mx-auto px-4">
      <div className="flex flex-wrap justify-between">
        <div
          className={
            "w-full md:w-1/2 mb-6 " + (post.id % 2 === 0 ? "md:order-last" : "")
          }
        >
          <img
            src={post.cover}
            alt={post.title}
            className="thumb-image w-full rounded-xl max-h-96 shadow-2xl"
          ></img>
        </div>
        <div className="w-full md:w-1/2 mb:1 md:mb-6 p-2 md:p-24">
          <div className="info flex gap-4 text-gray-400 italic">
            <span>
              <i className="fa fa-calendar text-xl mr-2"></i>
              {post.dateString}
            </span>
            <span>
              <i className="fa fa-pencil text-xl mr-2"></i>
              By {post.author}
            </span>
          </div>
          <div className="title">
            <h5 className="text-4xl ">{post.title}</h5>
          </div>
          <div className="short-desc">
            <HtmlRender
              html={post.content}
              excerptLength={EXCERPT_LENGTH}
              isExcerpt={true}
            />
          </div>
          <Link
            to={`./post/${post.id}`}
            className="text-blue-500 mt-2 block"
            aria-label={`Read more about ${post.title}`}
          >
            Read More
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Thumbnail;
