import React, { useState } from "react";

import { Link, useNavigate } from "react-router-dom";
import { AuthticationService } from "../services/auth";

function Navbar() {
  const navigate = useNavigate();

  const handleLogout = () => {
    setPagesOpen(false);
    AuthticationService.logout(() => {
      navigate("/login");
    });
  };
  const [isPagesOpen, setPagesOpen] = useState(false);
  const [isDestinationsOpen, setDestinationsOpen] = useState(false);

  return (
    <header className="bg-blue-600/[0.8] md:p-4 w-full sticky top-0 z-50 border-b-2 border-blue-600/[0.5]   shadow-xl">
      <nav
        className={`flex container justify-start p-1 align-center items-center h-12
           ${
             AuthticationService.isAuthenticated
               ? " md:justify-center"
               : " md:justify-end"
           }
          `}
      >
        {/* Logo */}

        {/* Mobile Menu Items */}
        <div className="md:hidden space-x-8 md:space-x-24">
          <div className="relative">
            {!AuthticationService.isAuthenticated && (
              <Link
                to="/"
                className="px-4 py-4 text-white font-bold"
                onClick={() => setPagesOpen(false)}
              >
                Home
              </Link>
            )}
            {AuthticationService.isAuthenticated && (
              <button
                onClick={() => setPagesOpen(!isPagesOpen)}
                onMouseEnter={() => setPagesOpen(!isPagesOpen)}
                onMouseLeave={() => setPagesOpen(!isPagesOpen)}
                className="text-white hover:text-gray-300 focus:outline-none text-5xl px-2"
              >
                ≡
              </button>
            )}
            {isPagesOpen && (
              <div
                className="absolute left-0 flex flex-col flex-wrap w-48 bg-blue-600/[0.8] rounded-lg shadow-lg font-bold text-2xl"
                onMouseEnter={() => setPagesOpen(true)}
                onMouseLeave={() => setPagesOpen(false)}
              >
                <Link
                  to=""
                  className="px-4 py-4 text-white hover:bg-gray-200"
                  onClick={() => setPagesOpen(false)}
                >
                  Home
                </Link>
                <Link
                  to="/about"
                  className="px-4 py-4 text-white hover:bg-gray-200"
                  onClick={() => setPagesOpen(false)}
                >
                  About
                </Link>
                <Link
                  to="/new-post"
                  className="px-4 py-4 text-white hover:bg-gray-200"
                  onClick={() => setPagesOpen(false)}
                >
                  Add New
                </Link>

                <div className="flex space-x-6">
                  <p
                    onClick={handleLogout}
                    className="px-4 py-4 text-white hover:bg-gray-200"
                  >
                    Logout
                  </p>
                </div>
              </div>
            )}
          </div>
        </div>
        {/* Desktop Menu Items */}
        <div className="hidden md:flex md:flex-wrap md:items-center place-items-center space-x-8 md:gap-12 font-serif">
          {AuthticationService.isAuthenticated && (
            <React.Fragment>
              <Link to="" className="text-white font-bold hover:text-gray-300">
                Home
              </Link>
              <Link
                to="/about"
                className="text-white font-bold hover:text-gray-300"
              >
                About
              </Link>
              <Link to="/" className="text-white font-bold text-4xl font-bold font-mono">
                Journey
              </Link>
              <Link to="/new-post" className="text-white font-bold">
                Add New
              </Link>
              <div className="flex space-x-6">
                <p
                  onClick={handleLogout}
                  className="text-white font-bold hover:text-gray-300 cursor-pointer"
                >
                  Logout
                </p>
              </div>
            </React.Fragment>
          )}
        </div>
      </nav>
    </header>
  );
}

export default Navbar;
